  mailIdVerifications() {
    if (this.addVanCustomer.value.email == "") {
      // this.toastr.warning("Email Id is Empty")
      this.mailError = false;
      // this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/mailIdVerification/' + this.addVanCustomer.value.email)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            // this.disableSignIn = false;
          } else {
            this.mailError = false;
            // this.disableSignIn = true;
          }
        })
    }
  }

  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
