import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute, RouterEvent, RouteConfigLoadStart, RouteConfigLoadEnd, NavigationCancel } from '@angular/router';


@Component({
  providers: [Beyond],
  selector: 'app-list-customer',
  templateUrl: './list-customer.component.html',
  styleUrls: ['./list-customer.component.scss']
})
export class ListCustomerComponent implements OnInit {
  customerList: any;
  searchCustomer: FormGroup;
  customerId: string;
  customer: any;
  customerInfoList: any;
  listcustomer: any;
  loginData: any;
  Customer: boolean;
  Name: boolean;
  PhoneNumber: boolean;
  Email: boolean;
  Address: boolean;
  Status: boolean;
  customerSatus: boolean = false;
  customerNameValue: any;
  customerArray: any = [];
  All: boolean;
  proId: void;
  form: FormGroup;
  
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private spinner: NgxSpinnerService) {

    this.router.events.subscribe((event: RouterEvent): void => {
      if (event instanceof RouteConfigLoadStart) {
        //console.log('The load started ')
        this.spinner.show()
      } else if (event instanceof RouteConfigLoadEnd ||
        event instanceof NavigationCancel) {
        this.spinner.hide()
        //console.log('The load ended ')
      } else {
        //console.log('The else load')
        this.spinner.hide()
      }
    })
    this.form = this.formBuilder.group({
      orders: new FormArray([])
    });
  }
  


  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log("loginData" + JSON.stringify(this.loginData.loginCode))
    this.proId = this.loginData.loginCode
    console.log("proid" + this.proId)
    this.listCustomerDetails()
    this.All = true;
      {
        this.searchCustomer = this.formBuilder.group({
          firstName: [null],
          customerId: [null],
          phoneNumber: [null],
          emailId: [null],
          address: [null],
          status: [null],
          selectBy: [null]
        })
      }
  }


  customerTypeSelection(x) {
    console.log("x" + x)
    if (x == "All") {
      this.listCustomerDetails()
    }
    if (x == "Customer") {
      this.Customer = true;
      this.Name = false;
      this.PhoneNumber = false;
      this.Email = false;
      this.Address = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "Name") {
      this.Customer = false;
      this.Name = true;
      this.PhoneNumber = false;
      this.Email = false;
      this.Address = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "PhoneNumber") {
      this.Customer = false;
      this.Name = false;
      this.PhoneNumber = true;
      this.Email = false;
      this.Address = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "Email") {
      this.Customer = false;
      this.Name = false;
      this.PhoneNumber = false;
      this.Email = true;
      this.Address = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "Address") {
      this.Customer = false;
      this.Name = false;
      this.PhoneNumber = false;
      this.Email = false;
      this.Address = true;
      this.Status = false;
      this.All = false;
    }
    if (x == "Status") {
      this.Customer = false;
      this.Name = false;
      this.PhoneNumber = false;
      this.Email = false;
      this.Address = false;
      this.All = false;
      this.Status = true;
    }
  }
  listCustomerDetails() {
    // this.spinner.show();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/vancustomerList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var customerData = data.json();
        console.log("customerData" + JSON.stringify(customerData))
        if (customerData.status == 200) {
          // this.spinner.hide();
          this.customerList = customerData.addressInfo;
          this.customerSatus = true;
          return false;
        } else {
          console.log("ELSE CONDITION IS CALLING")
          // this.spinner.hide();
          this.customerSatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  reset() {
    this.searchCustomer.reset();
    this.listCustomerDetails()
  }

  addCustomer() {
    this.router.navigate(['/proCustomer/addVanCustomer'])
  }

  customerWO(data) {
    localStorage.setItem('customerData', JSON.stringify(data))
    this.router.navigate(['/workorder/listWorkOrders'])
  }

  editCustomer(customerId) {
    this.router.navigate(['/proCustomer/editVanCustomer', customerId])

  }

  viewCustomer(customerId) {
    this.router.navigate(['/proCustomer/viewVanCustomer', customerId])
  }

  // Search Customer Starts
  searchCustomerData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let obj = {
      "customerId": this.searchCustomer.value.customerId,
      "firstName": this.searchCustomer.value.firstName,
      "phoneNumber": this.searchCustomer.value.phoneNumber,
      "emailId": this.searchCustomer.value.emailId,
      "address": this.searchCustomer.value.address,
      "status": this.searchCustomer.value.status
    }
    console.log(obj)
    this.http.post(Global.url + '/searchCustomer/' + this.proId, [
      { id: 'customerId', value: obj.customerId },
      { id: 'firstName', value: obj.firstName },
      { id: 'phoneNumber', value: obj.phoneNumber },
      { id: 'emailId', value: obj.emailId },
      { id: 'address', value: obj.address },
      { id: 'status', value: obj.status }
    ]).subscribe((data: any) => {
      this.customer = data.json();
      console.log("center" + JSON.stringify(this.customer))
      if (this.customer.status == 200) {
        this.customerList = this.customer.response;
        console.log("customerData values" + JSON.stringify(this.customerList))
        this.searchCustomer.reset();
      } else {
        console.log('The else')
      }
    })
  }
  // Search Customer Ends

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
