import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { AuthService } from '../../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-reset-customer-password',
  templateUrl: './reset-customer-password.component.html',
  styleUrls: ['./reset-customer-password.component.scss']
})
export class ResetCustomerPasswordComponent implements OnInit {
  resetPassword: any;
  loginResponse: any;
  passwordChangedResponse: any;
  constructor( public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,) { }

  ngOnInit() {
    this.resetPassword = this.formBuilder.group({
      Email: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  checkingEmail(){
    console.log("check email"+ this.resetPassword.value.Email)
    this.http.get(Global.url + '/gettingLoginCode/' + this.resetPassword.value.Email)
    .subscribe(data => {
      var mailResponse = data.json();
      if (mailResponse.status == 200) {
        this.loginResponse = mailResponse.response[0];
        console.log("loginResponse"+JSON.stringify(this.loginResponse))
      } else {
      }
    })
  }

  changePassword(){
    let obj={
      "loginCode": this.loginResponse.loginCode,
      "password": this.resetPassword.value.password,
    }
    console.log("obj"+JSON.stringify(obj))
    this.http.post(Global.url + '/customerResetPassword', obj)
    .subscribe(data => {
      var passwordResponse = data.json();
      if (passwordResponse.status == 200) {
        console.log("password Changed Response"+JSON.stringify(passwordResponse))
        this.router.navigate(['/auth/login/0'])
      } else if(passwordResponse.status == 404) {
        console.log("password Changed Response Failed")
      }
    })
  }

}
