  listWorkOrder() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/customerWorkOrder/' + this.customerId, options)
      .subscribe(data => {
        var woResponse = data.json();
        if (woResponse.status == 200) {
          console.log("group Details" + JSON.stringify(woResponse))
          this.bill = woResponse.billingAddress;
          console.log("list of workOrderList" + JSON.stringify(this.bill))
          this.woResponseStatus = true;
        } else {
          this.woResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  viewWorkOrder(workOrderNumber) {
    localStorage.setItem('workOrderDetails', JSON.stringify(this.bill))
    this.router.navigate(['/workorder/viewWorkOrder', workOrderNumber])
  }
  listEstimate() {
    console.log("in list estimate")
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfEstimatesForPro/' + this.loginData.loginCode + "/" + this.customerId, options)
      .subscribe(data => {
        var estimateResponse = data.json();
        console.log("estimate Details" + JSON.stringify(estimateResponse))
        if (estimateResponse.status == 200) {
          this.estimateList = estimateResponse.response;
          console.log("estimate" + JSON.stringify(this.estimateList))
          this.estimateResponseStatus = true;
        } else {
          this.estimateResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  viewImageData() {
    //console.log("view called" + this.partID)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCustomerViewImage/' + this.customerId, options)
      .subscribe(data => {
        var customerData = data.json();
        this.viewImage = customerData.response[0];
        console.log("vanData" + JSON.stringify(this.viewImage))
        if (this.viewImage.customerImage == "default_person.jpg") {

          this.imagePreview = "../../assets/img/avatars/default_person.jpg"

          console.log("resume" + this.imagePreview)
        } else {
          this.imagePreview = Global.url + "/" + this.viewImage.customerImage
          // resume1http://localhost:8888/uploads/33f46c179cb54c20bf5a0a5a05ea8188
          console.log("resume1" + this.imagePreview)
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

