  setAddress() {
    console.log("in set adress")
    let control = <FormArray>this.addAddressList.controls.fromAddressValues;
    console.log("length" + this.addressDataInfo.length)
    this.addressDataInfo.forEach((x, i) => {
      console.log(x)
      control.push(this.formBuilder.group({
        // proId: this.loginData.loginCode,
        firstName: x.firstName,
        lastName: x.lastName,
        userType: x.userType,
        email: x.emailId,
        phoneNumber: x.phoneNumber,
        phoneType: x.phoneType,
        dispatchTextNumber: x.dispatchTextNumber,
        propertyName: x.propertyName,
        sendInvoiceBy: x.sendInvoiceBy,
        address: x.address,
        invoiceEmail: x.invoiceEmail,
        unit: x.unit,
        zipCode: x.zipCode,
        city: x.city,
        state: x.state,
        crossSt1: x.crossSt1,
        crossSt2: x.crossSt2,
        adminNotes: x.adminNotes,
      }))
      if (i == this.addressDataInfo.length - 1) {
        return control;
      }

    })
  }

