import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddCustomerComponent } from './van-customers/add-customer/add-customer.component';
import { ListCustomerComponent } from './van-customers/list-customer/list-customer.component';
import { EditCustomerComponent } from './van-customers/edit-customer/edit-customer.component';
import { ViewCustomerComponent } from './van-customers/view-customer/view-customer.component';
import { ResetCustomerPasswordComponent } from './van-customers/reset-customer-password/reset-customer-password.component';
import { ProzCustomerLayoutComponent } from './proz-customer-layout/proz-customer-layout.component';

//import { ProzCustomerLayoutComponent } from './proz-customer-layout/proz-customer-layout.component';
const routes: Routes = [{
  path: '', component: ProzCustomerLayoutComponent,
  children: [
    {
      path: 'addVanCustomer',
      component: AddCustomerComponent
    },
    {
      path: 'resetPassword',
      component: ResetCustomerPasswordComponent
    },
    {
      path: 'listVanCustomers',
      component: ListCustomerComponent
    },
    {
      path: 'viewVanCustomer/:customerId',
      component: ViewCustomerComponent
    },
    {
      path: 'editVanCustomer/:customerId',
      component: EditCustomerComponent
    },

  ]
}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzCustomerRoutingModule { }
